* {
  padding: 0%;
  margin: 0%;
}

body {
  background-color: rgb(252, 249, 164);
  font-family: sans-serif;
}

.container .header {
  display: flex;
  align-items: center;
  justify-content: center;
}

.container .header img {
  width: 500px;
  height: 165px;
  margin-top: 12px;
}

.container .middlepart {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.container .middlepart .leftside img {
  width: 400px;
  height: 600px;
  margin-left: 20px;
  margin-right: 20px;
}

.container .middlepart .rightside .firstpara {
  font-size: 15px;
  font-weight: 600;
  margin: 8px;
}

.container .middlepart .rightside ul {
  font-size: 15px;
  margin-left: 30px;
  margin-bottom: 8px;
}

.container .middlepart .rightside p {
  font-size: 15px;
  margin-left: 8px;
}

.container .middlepart .rightside img {
  height: 500px;
  width: 900px;
}

.container .lowerpart {
  margin-top: 35px;
  text-align: center;
}

.container .lowerpart p {
  margin: 10px;
  font-size: 13px;
  font-weight: 600;
}

.container .lowerpart img {
  width: 1100px;
  height: 500px;
}

.container1 {
  text-align: center;
  margin-top: 20px;
  margin-left: 5px;
  margin-right: 5px;
  border-top: 3px solid red;
}

.container1 .firstcap {
  font-size: 18px;
  font-weight: 600;
  margin-top: 10px;
  margin-bottom: 10px;

}

.container1 .scaption1 {
  margin-bottom: 30px;
}

.container1 .scaption span {
  font-size: 13px;
  border-left: 1px solid red;
  padding: 0px 10px;
  margin-right: 7px;
}
.container1 .scaption span:first-child{
  border-left: none;
}
.container1 .scaption1 span:first-child{
  border-left: none;
}

.container1 .scaption1 span {
  font-size: 13px;
  border-left: 1px solid red;
  padding: 0px 20px;
  margin-right: 10px;


}

.footer {
  margin: 5px;
  width: 100%;
  height: 100px;
  display: flex;
  color: white;
  background-color: red;
}

.footer span {
  margin: auto;
  text-align: center;
}

